



















	var userLoginInfo;
	export default {
		data() {
			return {
				focus: false,
				content:''
			}
		},
		onShow() {
			userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		},
		onUnload() {
			
		},
		methods: {
			//提交反馈内容
			save() {
				uni.request({
					url: this.apiUrl + 'user/addFeedBack',
					method: 'POST',
					header:{
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						uid: userLoginInfo.id,
						content:this.content,
					},
					success: res => {
						
						if(res.data.code == 0){
							this.content= '',
							uni.showToast({
								title: "提交成功",
								icon: "none"
// 								success: (res) => {
// 									setTimeout(function () {
// 										uni.redirectTo({
// 											url:'./install'
// 										})
// 									}, 500);
// 								},
							});
						}	
					}
				});
			},
		}
	}
